body {
  margin: 0;
  padding: 0;
  min-width: 1200px;
  font-family: "Montserrat", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 23px;
  color: #636363;
}

a {
  color: #636363;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px hsl(0deg, 0%, 100%);
  -webkit-text-fill-color: #636363;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.city-list,
.site-navigation,
.audience-list,
.program-list,
.result-list,
.speaker-skills-list,
.gallery-list,
.tariff-list,
.main-footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 1180px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
}

.container-wide {
  width: 1420px;
}

.container-fluid {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container-fluid {
    padding: 0 32px;
  }
}
@media (max-width: 1023px) {
  .container-fluid {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container-fluid {
    padding: 0 16px;
  }
}

.section-title {
  font-weight: 700;
  font-size: 30px;
  line-height: 37px;
  color: #000000;
}
.section-title-result {
  margin: 0;
  padding-top: 150px;
  padding-bottom: 30px;
  color: hsl(0deg, 0%, 100%);
}

.main-header-wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.main-header-city-list {
  margin-right: 80px;
}
.main-header-register-button {
  margin-left: auto;
  margin-top: 35px;
}

.city-list {
  display: flex;
  flex-wrap: wrap;
  gap: 0 25px;
  width: 400px;
}
.city-list-item {
  padding-top: 40px;
  padding-bottom: 20px;
}
.city-list-item--footer {
  padding-top: 20px;
}
.city-list-link {
  font-size: 14px;
  line-height: 17px;
  color: hsl(0deg, 0%, 100%);
  text-transform: uppercase;
}
.city-list-link--header {
  color: #185fbf;
}

.site-navigation {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 0 40px;
  width: 520px;
}
.site-navigation-item {
  padding: 40px 0 20px;
}
.site-navigation-link {
  font-size: 14px;
  line-height: 17px;
  color: hsl(0deg, 0%, 100%);
}

.button {
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border-radius: 50px;
  padding: 10px;
}
.button--register-course {
  color: #2f80ed;
  background-color: hsl(0deg, 0%, 100%);
}
.button--register {
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
  background-color: #64e2cd;
  box-shadow: 0 0 0 10px rgba(100, 226, 205, 0.5);
}

.page-body {
  background-image: url("../../img/bg.png"), url("../../img/bg-2.png"), url("../../img/bg-3.png");
  background-repeat: no-repeat;
  background-position: top right;
}

.promo {
  padding-top: 60px;
  padding-bottom: 215px;
}
.promo-wrapper {
  display: flex;
  gap: 0 35px;
}
.promo-raiting-column {
  width: 565px;
  margin-left: 55px;
  margin-top: 300px;
}
.promo-text-column {
  width: 570px;
}
.promo-title {
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
  color: hsl(0deg, 0%, 100%);
}
.promo-text-compact {
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: hsl(0deg, 0%, 100%);
}

.rating {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 230px;
  padding: 40px;
  color: #2f80ed;
  border-radius: 50%;
  background: radial-gradient(68.31% 68.31% at 50% 50%, #f9fbff 0%, #cadef9 100%), hsl(0deg, 0%, 100%);
  box-shadow: 0 10px 50px rgba(119, 157, 255, 0.3);
}
.rating-highlight-text {
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 34px;
  line-height: 38px;
  color: #2f80ed;
}
.rating-text {
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #2f80ed;
  text-align: center;
}
.rating-link {
  font-weight: 400;
  font-size: 10px;
  line-height: 20px;
  color: #2f80ed;
}

.audience {
  padding-bottom: 200px;
}
.audience-list {
  display: flex;
  flex-wrap: wrap;
  gap: 75px 50px;
  justify-content: flex-start;
}
.audience-item {
  width: 225px;
}
.audience-item-category {
  margin-top: 30px;
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #2f80ed;
}
.audience-description {
  font-size: 14px;
  line-height: 20px;
}

.program {
  padding-bottom: 150px;
}
.program-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 45px 45px;
  width: 600px;
  margin-bottom: 10px;
}
.program-item {
  width: 250px;
}
.program-item-topic {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #2f80ed;
}
.program-item-description {
  font-size: 14px;
  line-height: 20px;
}
.program-link {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  color: #2f80ed;
}

.result {
  background-image: url("../../img/bg-21.png"), url("../../img/bg-22.png"), url("../../img/bg-23.png");
  background-repeat: no-repeat;
  background-position: top 80px left, top 40px left, top left;
  padding-bottom: 100px;
}
.result-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 35px 40px;
}
.result-item {
  width: 560px;
  padding: 45px 50px 50px 150px;
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0 10px 30px rgba(47, 55, 237, 0.1);
  border-radius: 10px;
}
.result-item-title {
  font-weight: 600;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #2f80ed;
}

.speaker {
  padding-bottom: 70px;
}
.speaker-wrapper {
  display: flex;
  margin-bottom: 30px;
}
.speaker-video {
  width: 727px;
  margin-right: 90px;
}
.speaker-img {
  border-radius: 10px;
}
.speaker-info {
  width: 350px;
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0 30px 100px rgba(47, 55, 237, 0.1);
  border-radius: 10px;
  margin-top: 50px;
  margin-bottom: 100px;
  text-align: center;
}
.speaker-name {
  font-weight: 700;
  font-size: 24px;
  line-height: 24px;
  color: #2f80ed;
}
.speaker-skills-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 70px 30px;
}
.speaker-skills-item {
  width: 240px;
}
.speaker-skill {
  font-weight: 600;
  font-size: 16px;
  line-height: 23px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #2f80ed;
}

.gallery {
  padding-bottom: 150px;
}
.gallery-title {
  text-align: center;
}
.gallery-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 12px 12px;
}
.gallery-item {
  width: 341px;
}
.gallery-item-img {
  width: 100%;
  height: auto;
}

.tariff {
  padding-bottom: 200px;
}
.tariff-title {
  text-align: center;
}
.tariff-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 25px 25px;
}
.tariff-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 271px;
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0 20px 50px rgba(47, 55, 237, 0.12);
  border-radius: 10px;
  padding: 40px;
}
.tariff-price {
  font-weight: 700;
  font-size: 25px;
  line-height: 40px;
  color: #2f80ed;
  margin: 0;
}
.tariff-description {
  font-weight: 600px;
  font-size: 16px;
  line-height: 25px;
  color: #2f80ed;
}
.tariff-link {
  font-weight: 700;
  font-size: 14px;
  line-height: 22px;
  color: hsl(0deg, 0%, 100%);
  background-color: #52d1bc;
  border-radius: 50px;
  padding: 10px 20px;
}

.main-footer {
  padding-top: 15px;
  padding-bottom: 45px;
  background-color: #2f80ed;
}
.main-footer-links-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-footer-copyright-wrapper {
  margin-top: 30px;
  padding: 15px;
  background-color: #185fbf;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
}
.main-footer-copyright-content {
  display: flex;
  justify-content: space-between;
}
.main-footer-copyright {
  width: 300px;
  margin: 10px 0;
}
.main-footer-links {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
  width: 460px;
  gap: 0 50px;
}
.main-footer-links-item {
  padding-top: 10px;
  padding-bottom: 10px;
}
.main-footer-link {
  color: inherit;
}

